@import url(https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sacramento&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Jost', sans-serif;
    font-size: 100%;
}

body{
    width: 100vw;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: #eee;
}

.box{
    background-color: rgba(19, 99, 190, 0.616);
    width: 25vw;
    height: 60vh;
    border-radius: 20px;
    box-shadow: 0 0.2rem 3rem rgb(153, 151, 151);
    position: relative;
    overflow: hidden;
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    min-width: 20rem;
    min-height: 35rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

input[type=search]{
    outline: none;
    border: 2px solid rgb(230, 227, 227);
    padding: 5px;
    text-align: center;
    font-weight: bold;
}

.info{
    color: #fff;
    position: absolute;
    top: 50%;
    overflow: hidden;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0 auto;
}

.null{
    text-align: center;
    font-size: 1.5rem;
    font-weight: bold;
    position: absolute;
    top: 50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}

.weather-icon{
    width: 100px;
    height: 100px;
}

.fas{
    margin: 0 10px;
    animation: upward 2s ease-in-out alternate-reverse infinite both;
}

@-webkit-keyframes upward {
    0%{
        -webkit-transform: translateX(10px);
                transform: translateX(10px);
    }
    100%{
        -webkit-transform: translateX(-10px);
                transform: translateX(-10px);
    }
}

@keyframes upward {
    0%{
        -webkit-transform: translateX(10px);
                transform: translateX(10px);
    }
    100%{
        -webkit-transform: translateX(-10px);
                transform: translateX(-10px);
    }
}
